.card-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 10%;
    margin-top: 5%;
    margin-right: 5%;
}

.card-container{
    border: 1px solid gray;
    width: 13rem;
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
    margin-right: 1rem;
    flex-direction: column;
    background-color: #95dada;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    -moz-osx-font-smootging: grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;

   
}
.card-container:hover {
    transform: scale(1.05);
}